import React from "react"

import Card from 'react-bootstrap/Card';
import Form from 'react-bootstrap/Form'
import FileDropZone from './FileDropzone'


//https://css-tricks.com/the-magic-of-react-based-multi-step-forms/

class Forms extends React.Component {
    constructor(props) {
      super(props)
      this.state = {
        currentStep: 1,
        organization:  '',
        username: '',
        password: '', 
      }
    }
  
    handleChange = event => {
      const {name, value} = event.target
      this.setState({
        [name]: value
      })    
    }
     
    handleSubmit = event => {
      event.preventDefault()
      const { organization, username, password } = this.state
      alert(`Your registration detail: \n 
             Organization: ${organization} \n 
             Username: ${username} \n
             Password: ${password}`)
    }
    
    _next = () => {
      let currentStep = this.state.currentStep
      currentStep = currentStep >= 2? 3: currentStep + 1
      this.setState({
        currentStep: currentStep
      })
    }
      
    _prev = () => {
      let currentStep = this.state.currentStep
      currentStep = currentStep <= 1? 1: currentStep - 1
      this.setState({
        currentStep: currentStep
      })
    }
  
  /*
  * the functions for our button
  */
  previousButton() {
    let currentStep = this.state.currentStep;
    if(currentStep !==1){
      return (
        <button 
          className="btn btn-secondary" 
          type="button" onClick={this._prev}>
        Previous
        </button>
      )
    }
    return null;
  }
  
  nextButton(){
    let currentStep = this.state.currentStep;
    if(currentStep <3){
      return (
        <button 
          className="btn btn-primary float-right" 
          type="button" onClick={this._next}>
        Next
        </button>        
      )
    }
    return null;
  }
    
    render() {    
      return (
        <Card style={{width: '72rem'}}>
        <Card.Body>
            <React.Fragment>        
            <p>Step {this.state.currentStep} </p> 
    
            <form onSubmit={this.handleSubmit}>
            {/* 
            render the form steps and pass required props in
            */}
            <Step1 
                currentStep={this.state.currentStep} 
                handleChange={this.handleChange}
                email={this.state.email}
            />
            <Step2 
                currentStep={this.state.currentStep} 
                handleChange={this.handleChange}
                username={this.state.username}
            />
            <Step3 
                currentStep={this.state.currentStep} 
                handleChange={this.handleChange}
                password={this.state.password}
            />            
            {this.previousButton()}
            {this.nextButton()}
    
            </form>
            </React.Fragment>        
        </Card.Body>        
        </Card>        
      );
    }
  }
  
  function Step1(props) {
    if (props.currentStep !== 1) {
      return null
    } 
    return(
      <div className="form-group">
        <h3>Organization</h3>        
        <Form.Label>Organization</Form.Label>                
        <Form.Control as="select">
          <option>Organization 1</option>        
          <option>Organization 2</option>        
          <option>Organization 3</option>        
        </Form.Control>
        <br></br>
        <label htmlFor="collection">Collection</label>
          <input
          className="form-control"
          id="collection"
          name="collection"
          type="text"
          placeholder="Enter your collection"
          value={props.collection}
          onChange={props.handleChange}
          />
      </div>
    );
  }
  
  function Step2(props) {
    if (props.currentStep !== 2) {
      return null
    } 
    return(
      <div className="form-group">
        <h3>Mandatory Metadata</h3>
        <label htmlFor="firstname">First Name of submitter</label>
        <input
          className="form-control"
          id="username"
          name="username"
          type="text"
          placeholder="First name"
          value={props.firstname}
          onChange={props.handleChange}
          />
        
        <br></br>
        <label htmlFor="lastname">Last Name of submitter</label>
        <input
          className="form-control"
          id="lastname"
          name="lastname"
          type="text"
          placeholder="Last name"
          value={props.lastname}
          onChange={props.handleChange}
          />
        
        <br></br>
        <label htmlFor="title">Title</label>
        <input
          className="form-control"
          id="title"
          name="title"
          type="text"
          placeholder="Title of object"
          value={props.title}
          onChange={props.handleChange}
          />
        
        <br></br>
        <label htmlFor="description">Description</label>
        <input
          className="form-control"
          id="description"
          name="description"
          type="text"
          placeholder="Description of object"
          value={props.description}
          onChange={props.handleChange}
          />
        
        <br></br>
        <label htmlFor="copyright">Copyright Status</label>
        <input
          className="form-control"
          id="copyright"
          name="copyright"
          type="text"
          placeholder="Copyright status of object"
          value={props.copyright}
          onChange={props.handleChange}
          />
      </div>
    );
  }
  
  function Step3(props) {
    if (props.currentStep !== 3) {
      return null
    } 
    return(
      <React.Fragment>     
      <div className="form-group">
        <h3>Metadata Elements</h3>
        <label htmlFor="keyword">Keyword/tags (separate with semicolon)</label>
        <input
          className="form-control"
          id="keyword"
          name="keyword"
          type="keyword"
          placeholder="Keyword/tags"
          value={props.keyword}
          onChange={props.handleChange}
          />      
        
        <br></br>
        <label htmlFor="date">Date (MM/DD/YYYY) </label>
        <input
          className="form-control"
          id="keyword"
          name="keyword"
          type="keyword"
          placeholder="Keyword/tags"
          value={props.keyword}
          onChange={props.handleChange}
          />      

        <br></br>
        <label htmlFor="creator">Creator</label>
        <input
          className="form-control"
          id="creator"
          name="creator"
          type="creator"
          placeholder="Creator"
          value={props.creator}
          onChange={props.handleChange}
          />

        <br></br>
        <label htmlFor="collection">Collection</label>
        <input
          className="form-control"
          id="collection"
          name="collection"
          type="collection"
          placeholder="Collection"
          value={props.collection}
          onChange={props.handleChange}
          />

        <br></br>
        <label htmlFor="publisher">Publisher</label>
        <input
          className="form-control"
          id="publisher"
          name="publisher"
          type="publisher"
          placeholder="Publisher"
          value={props.publisher}
          onChange={props.handleChange}
          />

        <br></br>
        <label htmlFor="language">Language</label>
        <input
          className="form-control"
          id="language"
          name="language"
          type="language"
          placeholder="Language"
          value={props.language}
          onChange={props.handleChange}
          />
      </div>
      <button className="btn btn-success btn-block">Submit</button>      
      <FileDropZone />
      </React.Fragment>      
    );
  }

export default Forms